/* General Page Styles */
.job-listings-page {
  font-family: 'Arial', sans-serif;
  color: #333;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

header.job-listings-header {
  text-align: center;
  margin-bottom: 40px;
}

header.job-listings-header h1 {
  font-size: 2.5em;
  margin: 0;
  color: #2c3e50;
}

header.job-listings-header p {
  font-size: 1.2em;
  color: #7f8c8d;
}

/* Add Job Section */
.add-job-section {
  background-color: #ecf0f1;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}

.add-job-section h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.add-job-form .form-group {
  margin-bottom: 20px;
}

.add-job-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  color: #34495e;
}

.add-job-form input,
.add-job-form textarea,
.add-job-form select {
  width: 100%;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease;
}

.add-job-form input:focus,
.add-job-form textarea:focus,
.add-job-form select:focus {
  border-color: #3498db;
}

.add-job-button {
  background-color: #3498db;
  color: #fff;
  padding: 12px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.add-job-button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.add-job-button:active {
  transform: translateY(0);
}

/* Filter Section */
.job-filters {
  margin-bottom: 40px;
}

.job-filters h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.job-filters .filters {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.job-filters input,
.job-filters select {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease;
}

.job-filters input:focus,
.job-filters select:focus {
  border-color: #3498db;
}

/* Job Listings Section */
.job-listings-section {
  background-color: #ecf0f1;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.job-listings-section h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.job-listings {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.job-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #dcdfe1;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.job-card h3 {
  font-size: 1.5em;
  margin: 0 0 10px 0;
  color: #2c3e50;
}

.job-card p {
  margin: 5px 0;
  color: #7f8c8d;
}

.job-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}
