/* General Navbar Styles */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #ffffff;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .navbar-logo a {
    font-size: 1.8rem;
    font-weight: bold;
    color: #007bff;
    text-decoration: none;
  }
  
  .navbar-links {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .navbar-links li {
    margin-left: 1.5rem;
  }
  
  .navbar-links a {
    color: #555;
    text-decoration: none;
    font-weight: 500;
    font-size: 1rem;
    transition: color 0.3s ease;
  }
  
  .navbar-links a:hover {
    color: #007bff;
  }
  
  /* Responsive Navbar */
  .navbar-toggle {
    display: none;
    font-size: 1.8rem;
    background: none;
    border: none;
    cursor: pointer;
    color: #007bff;
  }
  
  @media (max-width: 768px) {
    .navbar-links {
      display: none;
      flex-direction: column;
      background-color: #ffffff;
      position: absolute;
      top: 60px;
      right: 20px;
      width: 200px;
      box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
      padding: 1rem;
      border-radius: 4px;
    }
  
    .navbar-links.open {
      display: flex;
    }
  
    .navbar-links li {
      margin: 0.8rem 0;
    }
  
    .navbar-toggle {
      display: block;
    }
  }
  