/* General Styles for the Landing Page */
.landing-page {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  color: #333;
}

/* Hero Section */
.hero-section {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3rem 1.5rem;
  background: linear-gradient(135deg, #007bff, #0056b3);
  color: #fff;
}

.hero-content h1 {
  font-size: 2.8rem;
  margin-bottom: 1rem;
  line-height: 1.3;
  animation: fadeIn 1.5s ease-in-out;
  font-weight: bold;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  animation: fadeIn 2s ease-in-out;
}

.cta-button {
  display: inline-block;
  padding: 0.8rem 1.8rem;
  background-color: #28a745;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.cta-button:hover {
  background-color: #1e7e34;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Features Section */
.features-section {
  text-align: center;
  padding: 3rem 1.5rem;
  background-color: #f9f9f9;
}

.features-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  font-weight: bold;
  color: #0056b3;
}

.features {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.feature {
  max-width: 300px;
  text-align: center;
  background-color: #ffffff;
  padding: 1.8rem;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.feature:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.feature h3 {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  color: #333;
}

.feature p {
  font-size: 1rem;
  color: #555;
  line-height: 1.5;
}

/* Login Page */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #007bff, #0056b3);
}

.login-container {
  width: 100%;
  max-width: 400px;
  padding: 2.5rem;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  text-align: center;
  animation: fadeIn 1.5s ease-in-out;
}

.login-container h2 {
  margin-bottom: 1.5rem;
  color: #0056b3;
  font-size: 1.8rem;
  font-weight: bold;
}

.login-container input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  color: #333;
  outline: none;
  transition: all 0.3s ease;
}

.login-container input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.login-container .login-button {
  width: 100%;
  padding: 12px;
  background-color: #28a745;
  border: none;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-container .login-button:hover {
  background-color: #218838;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.login-container .redirect-text {
  margin-top: 1.5rem;
  font-size: 0.9rem;
  color: #333;
}

.login-container .redirect-text a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.login-container .redirect-text a:hover {
  text-decoration: underline;
}

/* Keyframe Animations */
@keyframes fadeIn {
  from {
      opacity: 0;
      transform: translateY(20px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
}

/* Responsive Styles */
@media (max-width: 768px) {
  .features {
      flex-direction: column;
      gap: 1.5rem;
  }

  .feature {
      width: 90%;
      margin: 0 auto;
  }

  .login-container {
      padding: 2rem;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
      font-size: 2rem;
  }

  .hero-content p {
      font-size: 0.9rem;
  }

  .cta-button {
      font-size: 0.9rem;
      padding: 0.6rem 1rem;
  }

  .login-container h2 {
      font-size: 1.5rem;
  }

  .login-container .login-button {
      font-size: 0.9rem;
      padding: 0.9rem;
  }
}
