/* General Styles */
.admin-dashboard {
  font-family: 'Arial', sans-serif;
  color: #333;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.dashboard-header {
  text-align: center;
  margin-bottom: 40px;
}

.dashboard-header h1 {
  font-size: 2.5em;
  margin: 0;
  color: #2c3e50;
}

.dashboard-header p {
  font-size: 1.2em;
  color: #7f8c8d;
}

/* Job Creation Section */
.job-creation {
  background-color: #ecf0f1;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}

.job-creation h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.job-creation form {
  display: flex;
  flex-direction: column;
}

.job-creation label {
  font-weight: bold;
  margin-bottom: 8px;
  color: #34495e;
}

.job-creation input,
.job-creation textarea,
.job-creation select {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease;
  margin-bottom: 20px;
}

.job-creation input:focus,
.job-creation textarea:focus,
.job-creation select:focus {
  border-color: #3498db;
}

.job-creation button {
  background-color: #3498db;
  color: white;
  padding: 12px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.job-creation button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.job-creation button:active {
  transform: translateY(0);
}

/* Job Management Section */
.job-management {
  margin-bottom: 40px;
}

.job-management h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin-bottom: 20px;
}

.job-table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.job-table th,
.job-table td {
  border: 1px solid #bdc3c7;
  padding: 10px;
  text-align: left;
}

.job-table th {
  background-color: #3498db;
  color: white;
  font-weight: bold;
}

.job-table td {
  background-color: #ecf0f1;
}

.job-table tr:hover {
  background-color: #dfe6e9;
}

/* Applications Management Section */
.applications-management {
  background-color: #ecf0f1;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.applications-management h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin-bottom: 20px;
}

.applications-table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.applications-table th,
.applications-table td {
  border: 1px solid #bdc3c7;
  padding: 10px;
  text-align: left;
}

.applications-table th {
  background-color: #e74c3c;
  color: white;
  font-weight: bold;
}

.applications-table td {
  background-color: #f9f9f9;
}

.applications-table tr:hover {
  background-color: #f1c40f;
}
